import Input from "./UI/myinput/Input";
import MyButton from "./UI/button/MyButton";
import axios from "axios";
import Header from "./Header";

const PatientForm = ({post, setPost, setEdit, updRightPanel, deletePatient}) => {


    const savePatient = async (e) => {
        e.preventDefault()
        const postPat = await axios.put(`http://127.0.0.1:8000/api-patients/patient-update/${post.id}/`, post)
        const postList = await axios.get('http://127.0.0.1:8000/api-patients/patient-list/')
        updRightPanel(postList.data)
        setEdit(false)
        setPost({first_name: '', last_name: '', date_birth: '', sex: '', state: '', country: ''})
    }

    return (
        <div>
            <Header>
                <span><MyButton onClick={savePatient}>Save</MyButton></span>
                <span><MyButton onClick={() => setEdit(false)}>Cancel</MyButton></span>
                <span><MyButton onClick={deletePatient}>Delete</MyButton></span>
            </Header>
            <form>
                <Input
                    value={post.first_name}
                    onChange={e => setPost({...post, first_name: e.target.value})}
                    type="text"
                    placeholder="First Name"
                />
                <Input
                    value={post.last_name}
                    onChange={e => setPost({...post, last_name: e.target.value})}
                    type="text"
                    placeholder="Last Name"/>
                <Input
                    value={post.date_birth}
                    onChange={e => setPost({...post, date_birth: e.target.value})}
                    type="date"
                    placeholder="Date birth"/>
                <select
                    value={post.sex}
                    onChange={e => setPost({...post, sex: e.target.value})}
                >
                    <option value="Male">Male</option>
                    <option value="Female">Female</option>
                </select>
                <Input
                    value={post.state}
                    onChange={e => setPost({...post, state: e.target.value})}
                    type="text"
                    placeholder="State"/>
                <Input
                    value={post.country}
                    onChange={e => setPost({...post, country: e.target.value})}
                    type="text"
                    placeholder="Country"/>
            </form>
        </div>
    );
};

export default PatientForm;