import React from 'react';
import {useState} from "react";
import Input from "./UI/myinput/Input";
import MyButton from "./UI/button/MyButton";
import axios from "axios";

const CreateForm = ({create}) => {
    const [post, setPost] = useState({first_name: '', last_name: '', date_birth: '', sex: '', state: '', country: ''});

    const addNewPost = async (e) => {
        e.preventDefault()

        const postPat = await axios.post('http://127.0.0.1:8000/api-patients/patient-create/', post)

        create(postPat.data)
        console.log(post)
        setPost({first_name: '', last_name: '', date_birth: '', sex: '', state: '', country: ''})
    }


    return (
        <div>
            <form>
                <Input
                    value={post.first_name}
                    onChange={e => setPost({...post, first_name: e.target.value})}
                    type="text"
                    placeholder="First Name"
                />
                <Input
                    value={post.last_name}
                    onChange={e => setPost({...post, last_name: e.target.value})}
                    type="text"
                    placeholder="Last Name"/>
                <Input
                    value={post.date_birth}
                    onChange={e => setPost({...post, date_birth: e.target.value})}
                    type="date"
                    placeholder="Date birth"/>
                {/*<MySelect
                    value={post.sex}
                    onChange={e => setPost({...post, sex: e.target.value})}
                    defaultValue="Choose sex"
                    option={[
                        {value: 'Male', name: 'Male'},
                        {value: 'Female', name: 'Female'},
                    ]}
                />*/}
                <select
                    value={post.sex}
                    onChange={e => setPost({...post, sex: e.target.value})}
                >
                    <option value="Male">Male</option>
                    <option value="Female">Female</option>
                </select>
                <Input
                    value={post.state}
                    onChange={e => setPost({...post, state: e.target.value})}
                    type="text"
                    placeholder="State"/>
                <Input
                    value={post.country}
                    onChange={e => setPost({...post, country: e.target.value})}
                    type="text"
                    placeholder="Country"/>

                <div className="position-btn">
                    <MyButton onClick={addNewPost}>Add patient</MyButton>
                </div>

            </form>
        </div>
    );
};

export default CreateForm;