import React, {useEffect, useState, useCallback} from 'react';
import axios from "axios";
import Header from "./Header";
import MyButton from "./UI/button/MyButton";
import PatientForm from "./PatientForm";

const PatientInfo = ({id, edit, setEdit, deletePatient, updRightPanel}) => {

    const [save, setSave] = useState(false);
    const [info, setInfo] = useState('')
    const getInfoPatient = useCallback(async () => {
        const response = await axios.get(`http://127.0.0.1:8000/api-patients/patient-detail/${id}/`)
        setInfo(response.data)
    }, [id])


    useEffect(() => {
        getInfoPatient()
    }, [id, edit, getInfoPatient])


    if(edit){
        return (
            <div>
                <PatientForm post={info} setPost={setInfo} setEdit={setEdit} updRightPanel={updRightPanel} save={save} setSave={setSave} deletePatient={deletePatient}/>
            </div>
        )
    }

    return (
        <div>
            <Header>
                <span><MyButton onClick={() => setEdit(true)}>Edit</MyButton></span>
                <span><MyButton onClick={deletePatient}>Delete</MyButton></span>
            </Header>
            <div className="info-patient">
                <p>First Name: {info.first_name}</p>
                <p>Last Name: {info.last_name}</p>
                <p>Country: {info.country}</p>
                <p>State: {info.state}</p>
                <p>Date of birth: {info.date_birth}</p>
            </div>
        </div>
    );
};

export default PatientInfo;